.drop-file-input {
  position: relative;
  width: 240px;
  border: 1px dashed rgba(0, 0, 0, 0.23);
  border-radius: 4px;

  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.drop-file-input input {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: opacity 0.3s;
}

.drop-file-input:hover,
.drop-file-input.dragover {
  opacity: 0.6;
}

.drop-file-input__label {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 10px;
  gap: 17px;
}

.drop-file-input__label img {
  width: 100%;
  max-height: 110px;
  border-radius: 4px;
}

.sublimeText {
  color: #F50057;
  text-decoration: solid;
}

.specText {
  width: 240px !important;
  font-size: 10px !important;
  line-height: 122% !important;
  padding: 10px;
  text-align: center;
}

.btnRemove {
  display: flex;
  gap: 9px;
  margin-top: 5px;
  color: #F50057;
  align-items: center;
  border: none;
  background: none;
  cursor: pointer;
  transition: opacity 0.3s;
}

.btnRemove:hover {
  opacity: 0.6;
}
